<template>
    <div>  
        <div class="loginNav">
            <img src="../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">写信</p>
        </div>
        <div class="c333 f28" style="padding:0.84rem 0 1.7rem;">
            <div class="nameBox b_line">
                <p>标题</p>
                <input class="name c666" placeholder="请输入" type="text">
            </div>
            <div class="content b_line">
                <p class="subtitle">内容</p>
                <textarea class="c666" placeholder="请输入内容"></textarea>
                <div class="picture mt-10">
                    <img src="../../assets/message/onShow.png" alt="">
                    <img src="../../assets/my/upload.png" alt="">
                </div>
            </div>
            <div class="nameBox b_line">
                <p>学生家长</p>
                <p class="c999" @click="$router.push({path:'/chooseParent'})">
                    请选择
                    <img src="../../assets/my/more.png" alt="">
                </p>
                
            </div>  
        </div>
        <div class="centerBtn f26 cfff text-center" @click="issue">发 布</div>
    </div>
</template>

<script>

export default {
  name: 'HelloWorld',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            data:{}
        }
    },
    methods : {
        returns(){
            this.$router.go(-1);//返回上一层
        },
        not(){
            this.active=true
            this.active1=false
        },
        yes(){
            this.active=false
            this.active1=true
        },
        lookDetail(index){
            var list=this.list;
            list[index].isSee=true;
            this.list=list;
            this.$router.push({path:'/leaveDetail',query:{active:this.active}})
        },
        issue(){

        }
    }
}
</script>

<style scoped>
     /* 修改组件样式 */
    .loginNav{
        position: fixed;
    }
    .van-tabbar-item--active {color: #8a9cff;}
    .nameBox{
        padding:0 .3rem;
        line-height: 1.1rem;
        display: flex;
        justify-content: space-between;
        align-items:center;
    }
    .nameBox img{
        width:.15rem;
        height:.26rem;
        vertical-align: middle;
    }
    .name{
        border:0;
        text-align: right;
        width:50%;
    }
    .content{
        padding:0 .3rem .3rem;
    }
    .subtitle{
        line-height: .9rem;
    }
    .content textarea{
        width:100%;
        height:4rem;
        border:0;
    }
    .picture img{
        width:1.2rem;
        height:1.2rem;
        margin-bottom:.1rem;
    }
    .centerBtn{
        height: 0.9rem;
        border-radius: 0.9rem; 
        background: linear-gradient(to bottom right, #82cbff , #7b7ceb);
        line-height: 0.9rem; 
        box-shadow: 0 0 10px #f0f9ff;
        position: fixed;
        bottom: 0.5rem;
        left:1rem;
        right:1rem;
    }
</style>




